import Vue from 'vue'

export default {
  state: {
    balance: 50000,
    estateBalance: { //     .   .  .
      buisness: {
        has: true
      },
      house: {
        has: true
      },
      bill: {
        has: true
      },
      phone: {
        has: true
      },
    },
    commissionPercent: 4, 
    catalogList: [
      {title: ' ',id:'buisness',  pathName: 'Payment-Buisness'},
      {title: ' ', id:'house', pathName: 'Payment-Home'},
      {title: '  ', id:'bill', pathName: 'Payment-Send'},
      {title: ' ', id:'phone', pathName: 'Payment-Phone'},
    ],
    catalogIcons: {
      ' ': '#bank-buisness',
      ' ': '#buisness-home',
      ' ': '#buisness-phone',
      '  ': '#buisness-money-bag',
      ' ': '#buisness-atm'
    },
    history: [
      {title: ' ', type: 'deposit',money: 32344, time: 1608579986991, checked: false},
      {title: ' ', type: 'withdraw',money: 144, time: 1608579985991},
    ]
  },
  mutations: {
    MAKE_TRANSACTION(state, data) {
      data.time = new Date().getTime()
      state.balance -= data.money
      state.history.push(data)
    },
    MAKE_DEPOSIT(state, data) {
      data.time = new Date().getTime()
      data.checked = false
      state.history.push(data)
      state.balance += data.money
    },
    CHECK_HISTORY_PAYMENTS(state) {
      state.history.map(item => Vue.delete(item, 'checked'))
    }
  },
  getters: {
    GET_UNCHECK_HISTORY(state) {
      let acc = 0;
      state.history.map(({checked}) => checked === false ? acc += 1 : null)
      return acc
    }
  }
}